export function createOrderOnlinePageElem() {
  // <div class="restaurant-location">
  //   <h1>FIND LOCATION</h1>
  //   <div class="restaurant-location-item-container">
  //     ...<restaurantLocationItem>...
  //   </div>
  // </div>

  const restaurantLocationDiv = document.createElement('div');
  const locationH1 = document.createElement('h1');
  const locationItemContainerDiv = document.createElement('div');

  restaurantLocationDiv.className = 'restaurant-location';
  locationH1.textContent = 'FIND LOCATION';
  locationItemContainerDiv.className = 'restaurant-location-item-container';

  restaurantLocationDiv.appendChild(locationH1);
  restaurantLocationDiv.appendChild(locationItemContainerDiv);

  // for every restaurant location item...

  const restaurantLocationItems = [
    {
      city: 'Fayetteville',
      address: '2897 N College Ave, #200',
      cityState: 'Fayetteville, Arizona',
    },
    {
      city: 'Culver City',
      address: '6000 Sepulveda Blvd',
      cityState: 'Culver City, California',
    },
    {
      city: 'Maple Grove',
      address: '7885 Wedgewood Ln',
      cityState: 'Maple Grove, Minnesota',
    },
    {
      city: 'Orlando',
      address: '100 W Livingston St, #400',
      cityState: 'Orlando, Florida',
    },
    {
      city: 'Columbia City',
      address: '116 N Chauncey St',
      cityState: 'Columbia City, Iowa',
    },
    {
      city: 'Coconut Creek',
      address: '4829 Coconut Creek Pkwy, #700',
      cityState: 'Coconut Creek, Florida',
    },
  ];

  // <div class="restaurant-location-item">
  //   <h2>CITY</h2>
  //   <p>
  //     ADDRESS, #STE
  //     <br />
  //     CITY, STATE
  //   </p>
  //   <button>Order</button>
  // </div>

  for (const restaurantLocationItem of restaurantLocationItems) {
    const elem = document.createElement('div');
    const h2 = document.createElement('h2');
    const p = document.createElement('p');
    const button = document.createElement('button');

    elem.className = 'restaurant-location-item';
    h2.textContent = restaurantLocationItem.city;
    p.innerHTML = `${restaurantLocationItem.address}<br/>${restaurantLocationItem.cityState}`;
    button.textContent = 'Order';

    elem.appendChild(h2);
    elem.appendChild(p);
    elem.appendChild(button);

    locationItemContainerDiv.appendChild(elem);
  }

  return restaurantLocationDiv;
}
