import { displayController } from './displayController';

(function main() {
  const navHome = document.querySelector('nav ul li#home');
  const navMenu = document.querySelector('nav ul li#menu');
  const navOrderOnline = document.querySelector('nav ul li#order-online');

  const { pages, switchPage } = displayController;

  navHome.onclick = () => {
    switchPage(pages.home);
  };
  navMenu.onclick = () => {
    switchPage(pages.menu);
  };
  navOrderOnline.onclick = () => {
    switchPage(pages.orderOnline);
  };

  switchPage(pages.home);
  // switchPage(pages.orderOnline);
})();
