import { createHomePageElem } from './pages/home';
import { createMenuPageElem } from './pages/menu';
import { createOrderOnlinePageElem } from './pages/orderOnline';

export const displayController = (function () {
  const pageContentDiv = document.querySelector('div#content');

  const pages = Object.freeze({
    home: createHomePageElem(() => switchPage(pages.menu)),
    menu: createMenuPageElem(),
    orderOnline: createOrderOnlinePageElem(),
  });

  function switchPage(page) {
    if ([...Object.values(pages)].includes(page)) {
      pageContentDiv.replaceChildren(page);
    } else {
      throw new Error('Unknown page accessed');
    }
  }

  return Object.freeze({ pages, switchPage });
})();
