export function createMenuPageElem() {
  // <div class="restaurant-menu">
  //   <h1>MENU</h1>
  //   <div class="menu-item-container">
  //     <restaurantMenuItem>
  //     ...many more items
  //   </div>
  // </div>;

  const restaurantMenuDiv = document.createElement('div');
  const menuH1 = document.createElement('h1');
  const menuItemContainerDiv = document.createElement('div');

  restaurantMenuDiv.className = 'restaurant-menu';
  menuH1.textContent = 'MENU';
  menuItemContainerDiv.className = 'menu-item-container';

  restaurantMenuDiv.appendChild(menuH1);
  restaurantMenuDiv.appendChild(menuItemContainerDiv);

  // for every restaurant menu item...

  const restaurantMenuItems = [
    {
      imageSource: './images/food/crunchy_roll.jpg',
      title: 'Chicken Crunch Roll',
      description: 'Chicken tempura, cream cheese, jalapeño, avocado & crunch',
      price: '$10.95',
    },
    {
      imageSource: './images/food/cream_cheese_combo.jpg',
      title: 'Cream Cheese Combo',
      description:
        'Cream cheese, smoked salmon & cucumber roll paired with a Cream cheese, tuna, cucumber & sesame seed roll.',
      price: '$17.95',
    },
    {
      imageSource: './images/food/crab_rangoon.jpg',
      title: 'Crab Rangoon',
      description:
        'Crab and cream cheese wrapped in a wonton. Served with sweet & sour sauce.',
      price: '$4.95',
    },
    {
      imageSource: './images/food/soy_bean_soup.jpg',
      title: 'Tofu & Vegetable Soup',
      description:
        'Tofu, spinach, artichoke, bell pepper, soy bean paste, garlic & salt.',
      price: '$7.95',
    },
    {
      imageSource: './images/food/sushi_boat.jpg',
      title: 'Sushi Boat',
      description:
        'To help you fulfill your sailor fantasy while eating sushi.',
      price: '$24.95',
    },
    {
      imageSource: './images/food/party_platter.jpg',
      title: 'Party Platter',
      description: 'A lotta sushi my dude.',
      price: '$22.95',
    },
  ];

  // <div class="restaurant-menu-item">
  //   <img src="./images/food/crunchy_roll.jpg" alt="Crunchy Sushi Roll" />
  //   <h2>Chicken Crunch Roll</h2>
  //   <p>Chicken tempura, cream cheese, jalapeño, avocado & crunch</p>
  //   <div class="price">$10.95</div>
  // </div>

  for (const restaurantMenuItem of restaurantMenuItems) {
    const elem = document.createElement('div');
    const img = document.createElement('img');
    const h2 = document.createElement('h2');
    const p = document.createElement('p');
    const price = document.createElement('div');

    elem.className = 'restaurant-menu-item';
    img.src = restaurantMenuItem.imageSource;
    img.alt = restaurantMenuItem.description;
    h2.textContent = restaurantMenuItem.title;
    p.textContent = restaurantMenuItem.description;
    price.className = 'price';
    price.textContent = restaurantMenuItem.price;

    elem.appendChild(img);
    elem.appendChild(h2);
    elem.appendChild(p);
    elem.appendChild(price);

    menuItemContainerDiv.appendChild(elem);
  }

  return restaurantMenuDiv;
}
