export function createHomePageElem(viewMenuButtonHandler) {
  // <div class="hero">
  //   <img
  //     src="./images/bar1.jpg"
  //     alt="Picture of dining tables and bar in sushi restaurant."
  //   />
  //   <div class="hero-text">
  //     <h1>Sushi Restaurant Group</h1>
  //     <h3>Japanese Diner · Sushi · Bar</h3>
  //     <button class="hero-button">View Menu</button>
  //   </div>
  // </div>

  const heroDiv = document.createElement('div');
  const heroImg = document.createElement('img');
  const heroTextDiv = document.createElement('div');
  const restaurantNameH1 = document.createElement('h1');
  const restaurantCategoryH3 = document.createElement('h3');
  const viewMenuButton = document.createElement('button');

  heroDiv.className = 'hero';
  heroImg.src = './images/bar1.jpg';
  heroImg.alt = 'Picture of dining tables and bar in sushi restaurant.';
  heroTextDiv.className = 'hero-text';
  restaurantNameH1.textContent = 'Sushi Restaurant Group';
  restaurantCategoryH3.textContent = 'Japanese Diner · Sushi · Bar';
  viewMenuButton.className = 'hero-button';
  viewMenuButton.textContent = 'View Menu';
  viewMenuButton.onclick = viewMenuButtonHandler;

  heroDiv.appendChild(heroImg);
  heroDiv.appendChild(heroTextDiv);
  heroTextDiv.appendChild(restaurantNameH1);
  heroTextDiv.appendChild(restaurantCategoryH3);
  heroTextDiv.appendChild(viewMenuButton);

  return heroDiv;
}
